@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: 0;
    outline: 0;
    text-decoration: none;
    font-size: 100%;
    vertical-align: baseline;
    font-family: 'Poppins', sans-serif;
}

html {
    scroll-behavior: smooth;
}

section {
    min-height: 100vh;
    min-width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}


.base {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100vh;
    min-width: 100vw;
    overflow: hidden;
    z-index: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

header {
    position: absolute;
    width: 85vw;
    top: 12.5vh;
    z-index: 10000;
    padding: 20px 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
}

header ul {
    display: -webkit-flex;
    display: flex;
}

header ul li {
    list-style: none;
    margin: 0 1em;
}

header ul li a {
    text-decoration: none;
    font-weight: 500;
    padding: 8px 15px;
    border-radius: 15px;
    color: white;
    font-size: 20px;
}

header ul li a:hover {
    color: black;
    background: #fff;
}

.projects_container {
    position: absolute;
    top: 16vh;
    min-width: 90vw;
    min-height: 75vh;
    max-height: 75vh;
    z-index: -1;
    background: rgba(100, 100, 100, 0.5);
    box-shadow: 0 15px 35px rgba(255, 255, 255, 0.05);
    border-radius: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 6vh;
    overflow: scroll;
}

.projects_container ul {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 2rem;
}

.project_card {
    padding: 20px;
    min-height: 200px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 5px 15px 35px rgba(0, 0, 0, 1);
    border-radius: 15px;
}

.project_card h1 {
    font-size: 1.2em;
    color: white;
}

.project_card p {
    font-size: 1em;
    color: white;
}
.skills-container {
    position: absolute;
    /*width: calc(100vw - 200px);*/
    width: 88vw;
    /*height: 90vh;*/
    z-index: -1;
    /*min-height: calc(100vh - 280px);*/
    /*min-width: 300px;*/
    background: rgba(100, 100, 100, 0.5);
    box-shadow: 0 15px 35px rgba(255, 255, 255, 0.05);
    border-radius: 20px;
    margin-top: 0.5rem;
}

.skills-content {
    /*margin-top: -100px;*/

    -webkit-align-items: center;

            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    /*display: flex;*/
}

.skills-cards {
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-auto-rows: minmax(100px, auto);
    grid-auto-flow: dense;
}

