.projects_container {
    position: absolute;
    top: 16vh;
    min-width: 90vw;
    min-height: 75vh;
    max-height: 75vh;
    z-index: -1;
    background: rgba(100, 100, 100, 0.5);
    box-shadow: 0 15px 35px rgba(255, 255, 255, 0.05);
    border-radius: 20px;
    display: flex;
    justify-content: center;
    padding: 6vh;
    overflow: scroll;
}

.projects_container ul {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 2rem;
}

.project_card {
    padding: 20px;
    min-height: 200px;
    backdrop-filter: blur(10px);
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 5px 15px 35px rgba(0, 0, 0, 1);
    border-radius: 15px;
}

.project_card h1 {
    font-size: 1.2em;
    color: white;
}

.project_card p {
    font-size: 1em;
    color: white;
}